  *{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  
  margin:0;
  font-family: 'Anton', sans-serif;
  padding: 0;
  text-align: center;
  background-color: rgb(48, 38, 53);
  color:#8855ff;
}

.App-link {
  color: #f8fb61;
}

.header{
  width: 100%;
  margin:0;
  margin-top: 30px;
} 


.img-container{
  position: relative;
  overflow: hidden;
  width: 80vw;
  height: 70vh;
  margin: 0 auto;
  border-radius: 15px;
}


.header-pic{
  position: absolute;
  display: block;
  padding: 0;
  top: -500px;
  left: -500px;
  border-radius: 15px;
}

.gallery-container{

  display:grid;
  grid-template-columns: repeat(3,1fr);
  grid-auto-rows: 150px;
  height: 50%;
  width: 100%;
  display:flex;
  padding: 60px;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
}

.gallery-img{
  width:500px;
  height:450px;
  margin-left: 0;
  margin-top: 25px;
  margin-bottom: 25px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.details-link{
  color:#8855fffd;
  letter-spacing: 4px;
  text-decoration: none;
  margin:10%;

}

.form-btn{
  font-family: inherit;
  font-weight: inherit;
  letter-spacing: 5px;
  padding: 0.5em 1em;
  margin-left: 1em;
  background-color: #9c74f8d7;
  box-shadow: 0 0 5px 0 rgba(234, 241, 248, 0.884);
  border: none;
  border-radius: 2px;
  cursor: pointer;
  color: #e3f2ffc9;
  transition: all 0.2s ease-in-out;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  margin-top:20px;
  margin-bottom:0;
}

.new-btn{
  font-family: inherit;
  font-weight: inherit;
  letter-spacing: 5px;
  padding: 0.5em 1em;
  margin-left: 1em;
  background-color: #9c74f8d7;
  box-shadow: 0 0 5px 0 rgba(234, 241, 248, 0.884);
  border: none;
  border-radius: 2px;
  cursor: pointer;
  color: #e3f2ffc9;
  transition: all 0.2s ease-in-out;
  position: -webkit-sticky;
  position: sticky;
